export default {
  name: "FAQView",
  data() {
    return {
      search: ""
    };
  },
  computed: {
    questions() {
      return this.$store.getters['faqs'];
    }
  },
  methods: {
    customFilter(a, needle, item) {
      const title = this.$t(`faqs.${item.raw}.title`);
      const text = this.$t(`faqs.${item.raw}.text`);
      return title.includes(needle) || text.includes(needle);
    }
  }
};